h1,
h2,
h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.container {
  max-width: 140rem;
  width: 95%;
  margin: 0 auto;
}

p {
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.section {
  padding: 7.2rem 0 1.6rem 0;
}
@media screen and (min-width: 768px) {
  .section {
    padding: 10.2rem 0 2.6rem 0;
  }
}

.main {
  overflow: hidden;
}

.header {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  height: 65px;
  width: 100%;
  z-index: 100;
  background-color: transparent;
  border-bottom: 1px solid #ebeaea;
}
@media screen and (min-width: 768px) {
  .header {
    border-bottom: none;
    display: block;
  }
}

.nav {
  margin-top: 0.5rem;
  height: 55px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .nav {
    height: calc(55px + 1rem);
  }
}
.nav__toggle {
  color: #000;
  font-size: 1.8rem;
  cursor: pointer;
  display: inline-flex;
}
@media screen and (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}
.nav__logo {
  color: #FFFFFF;
  font-size: 2.7rem;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.nav__logo--icon {
  color: #e8505b;
}
.nav__button--content {
  display: none;
  color: #FFFFFF;
  font-size: 1.6rem;
  padding: 1rem 1.5rem;
  border-radius: 2rem;
}
@media screen and (min-width: 768px) {
  .nav__button--content {
    display: block;
  }
}
.nav__button--content:hover {
  transition: 0.3s;
  color: #000;
  background-color: #ebeaea;
}

@media screen and (max-width: 768px) {
  .menu {
    position: fixed;
    top: 0;
    right: -100%;
    height: 100%;
    width: 100%;
    background-color: #ebeaea;
    transition: 0.5s;
  }
}
.menu__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 4rem;
  padding-top: 25rem;
}
@media screen and (min-width: 768px) {
  .menu__list {
    flex-direction: row;
    padding-top: 0;
    column-gap: 2rem;
  }
}
.menu__link {
  position: relative;
  color: #FFFFFF;
  font-size: 1.8rem;
  text-transform: capitalize;
  transition: 0.3s;
}
@media screen and (min-width: 768px) {
  .menu__link {
    font-size: 1.6rem;
  }
}
.menu__link:hover {
  position: relative;
}
.menu__link:hover::after {
  content: "";
  background-color: #e8505b;
  position: absolute;
  top: 2.4rem;
  left: 0;
  height: 2px;
  width: 100%;
  margin: auto 0;
}
@media screen and (min-width: 768px) {
  .menu {
    font-size: 1.6rem;
  }
}
.menu__close {
  position: absolute;
  top: 2rem;
  right: 2rem;
  font-size: 1.8rem;
}
@media screen and (min-width: 768px) {
  .menu__close {
    display: none;
  }
}

.form {
  margin: 5rem auto 2rem auto;
  background-color: #FFFFFF;
  border-radius: 5rem;
  box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.3);
  padding: 3rem 1rem;
}
@media screen and (min-width: 1024px) {
  .form {
    display: flex;
    justify-content: space-between;
    max-width: 90rem;
    padding: 0;
    margin: 9rem auto 2rem auto;
  }
}
.form__campo {
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 1024px) {
  .form__campo:nth-child(1) {
    padding-left: 4rem;
  }
}
.form__campo:hover {
  background-color: #ebeaea;
  border-radius: 5rem;
}
.form__label {
  font-size: 1.3rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  text-transform: capitalize;
}
.form__input {
  border: none;
  width: 100%;
}
.form__input::placeholder {
  font-size: 1.4srem;
}
.form__button {
  background-image: radial-gradient(circle, rgb(255, 56, 92) 0%, rgb(189, 30, 89) 100%);
  text-transform: uppercase;
  color: #FFFFFF;
  font-weight: 500;
  padding: 0 2rem;
  font-size: 1.5rem;
  height: 4rem;
  border: none;
  border-radius: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 0.5rem;
  cursor: pointer;
}

.home {
  background-image: url(../img/header.jpg);
  background-size: cover;
  background-position: center;
  padding: 3rem 0 19.5rem 0;
}
.home__search {
  margin-top: 5rem;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .home__search {
    margin-top: 30rem;
  }
}
.home__search--button {
  background-color: #FFFFFF;
  padding: 2rem 4rem;
  color: #e8505b;
  margin-top: 1rem;
  border-radius: 3rem;
  font-size: 2rem;
  font-weight: 700;
  display: block;
}
@media screen and (min-width: 768px) {
  .home__search--button {
    display: inline-block;
  }
}

.dest__title {
  font-size: 2.5rem;
}
@media screen and (min-width: 768px) {
  .dest__title {
    font-size: 3.2rem;
  }
}
.dest__grid {
  margin-top: 4rem;
}
@media screen and (min-width: 768px) {
  .dest__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
  }
}
@media screen and (min-width: 1024px) {
  .dest__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
    align-items: center;
  }
}
.dest__flex {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  column-gap: 2rem;
}
.dest__img {
  width: 14rem;
}
.dest__name {
  font-size: 2rem;
  margin: 0;
}
@media screen and (min-width: 768px) {
  .dest__name {
    font-size: 2.2rem;
  }
}
@media screen and (min-width: 1024px) {
  .dest__name {
    font-size: 2.4rem;
  }
}
.dest__hours {
  font-size: 1.6rem;
  margin: 0;
  color: #000;
}
@media screen and (min-width: 768px) {
  .dest__hours {
    font-size: 1.8rem;
  }
}
@media screen and (min-width: 1024px) {
  .dest__hours {
    font-size: 2rem;
  }
}

.live {
  padding-bottom: 4rem;
}
.live__title {
  margin-bottom: 5rem;
}
.live__grid {
  display: grid;
  grid-template-columns: repeat(4, 30rem);
  column-gap: 4rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
@media screen and (min-width: 768px) {
  .live__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    overflow-y: unset;
    overflow-x: unset;
  }
}
@media screen and (min-width: 1024px) {
  .live__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.live__content {
  scroll-snap-align: center;
}
.live__image {
  border-radius: 1rem;
}
.live__info {
  font-size: 1.6rem;
}
@media screen and (min-width: 768px) {
  .live__info {
    font-size: 1.8rem;
  }
}

.hostess {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  row-gap: 15rem;
  padding: 5rem;
  background-image: linear-gradient(to bottom, #000 0%, #000 50%, transparent 0), url(../img/anfitrion.jpg);
  background-size: 100%, 60rem;
  background-position: 50%, bottom;
  background-repeat: no-repeat;
}
@media screen and (min-width: 480px) {
  .hostess {
    background-size: 100%, 70rem;
    background-position: center, center 200%;
  }
}
@media screen and (min-width: 768px) {
  .hostess {
    grid-template-rows: unset;
    background-image: linear-gradient(to right, #000 0, #000 45%, transparent 55%), url(../img/anfitrion.jpg);
    background-size: 100%, auto;
    background-position: center, 50% 30%;
    padding: 15rem 5rem;
  }
}
@media screen and (min-width: 1024px) {
  .hostess {
    background-position: center, right center;
  }
}
@media screen and (min-width: 768px) {
  .hostess__content {
    max-width: 50%;
  }
}
.hostess__title {
  color: #FFFFFF;
  line-height: 1.2;
}
.hostess__info {
  color: #FFFFFF;
  font-size: 1.8rem;
}
.hostess__button {
  background-color: #FFFFFF;
  border-radius: 10px;
  display: block;
  margin-top: 1rem;
  padding: 1rem;
  color: #000;
  font-weight: 500;
  font-size: 1.8rem;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .hostess__button {
    display: inline-block;
    padding: 1rem 4rem;
  }
}

.experience {
  margin-bottom: 4rem;
}
.experience__destp {
  font-size: 1.8rem;
  margin-bottom: 5rem;
}
.experience__grid {
  display: grid;
  grid-template-columns: repeat(3, 30rem);
  column-gap: 4rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
@media screen and (min-width: 768px) {
  .experience__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    overflow-x: unset;
    overflow-y: unset;
  }
}
@media screen and (min-width: 1024px) {
  .experience__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.experience__content {
  scroll-snap-align: center;
}
.experience__img {
  border-radius: 1rem;
}
.experience__subtitle {
  color: #000;
  font-weight: 600;
}
.experience__info {
  font-size: 1.7rem;
}

.footer {
  background-color: #fcfcfc;
  border-top: 1px solid #cecece;
  padding-top: 5rem;
}
@media screen and (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
    margin-bottom: 3rem;
  }
}
@media screen and (min-width: 1024px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 4rem;
  }
}
.footer__widget {
  margin-bottom: 2rem;
}
@media screen and (min-width: 768px) {
  .footer__widget {
    margin-bottom: 0;
  }
}
.footer__title {
  font-size: 1.5rem;
  margin-bottom: 2rem;
}
.footer__items {
  margin-bottom: 1.5rem;
}
@media screen and (min-width: 768px) {
  .footer__items:last-of-type {
    margin-bottom: 0;
  }
}
.footer__links {
  font-size: 1.2rem;
  color: #686868;
}

.copy {
  border-top: 1px solid #686868;
}
.copy__flex {
  margin: 2rem 0;
}
@media screen and (min-width: 768px) {
  .copy__flex {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    column-gap: 1rem;
  }
}
.copy__item {
  margin-bottom: 1rem;
}
.copy__item:last-of-type {
  margin-bottom: 0;
}
.copy__link {
  font-size: 1.2rem;
  color: #686868;
}
.copy__under {
  display: none;
}
@media screen and (min-width: 768px) {
  .copy__under {
    text-align: left;
    margin-left: 1rem;
    display: inline-block;
  }
}